import React, {Component} from "react";
import CarouselBox from "../components/CarouselBox";
import {CardDeck, Container, Card, Button} from "react-bootstrap";

export default class Home extends Component{
    render() {
        return (
            <>
                <CarouselBox/>
                <Container>
                    <h2 className="text-center m-4">Информация</h2>
                    <CardDeck className="m-4">
                        <Card border="dark">
                            <Card.Img
                                variant="top"
                                className="d-block w-100"
                                src="https://lh3.googleusercontent.com/proxy/J2TGucB2p5ZiS5mfRAsWVQftK8T1rfthEnvrjJPzKdR4uUu04sxkH8FFk0mhSt5l62zAUC242K5-PWfDZwQ-H7OjM7TTCxZS3H5ixFIynrJRY8ZoOCtuqqGY8OYXgawtyY_s8MyFW3jSF7-jXg_O0NAEMlqo"
                            />
                            <Card.Body>
                                <Card.Title>Команда</Card.Title>
                                <Card.Text>
                                    Наши ФИО
                                </Card.Text>
                                <Button variant="primary">О команде</Button>
                            </Card.Body>
                        </Card>

                    </CardDeck>
                </Container>
            </>
        )
    }
}